const slider = document.createElement('ul');
slider.classList.add('slider');

// add slide item structure
function addSliderItem() {
  const li = document.createElement('li');
  li.className = 'item';
  li.innerHTML = `
  <div>
    <img draggable="false">
  </div>
  <div>
    <span class='icon'><i class="fas fa-male"></i></span>
    <p></p>
  </div>
  <div>
    <span class='icon'><i class="far fa-calendar-alt"></i></span>
    <p></p>
  </div>
  <div>
    <span class='icon'><i class="far fa-eye"></i></span>
    <p></p>
  </div>
  <p class='description'></p>
  <span></span>
  `;

  slider.appendChild(li);
}


// add informations from second request (view count)
function addViewCount(res) {
  const listViewCount = document.querySelectorAll('.fa-eye');
  for (let i = 0; i < res.length; i += 1) {
    listViewCount[i].parentElement.parentElement.lastElementChild.textContent = res[i].statistics.viewCount;
  }
}

// add informations
let oldSizeSlider = 0;
let prevInputValue;

function addInfo(items, key, inputValue) {
  const arrId = [];

  // refill slider if inputValue became another
  if (prevInputValue !== inputValue) {
    oldSizeSlider = 0;
  }

  // add information from first request
  const listImg = document.querySelectorAll('img');
  const listChannelName = document.querySelectorAll('.fa-male');
  const listUploadDate = document.querySelectorAll('.fa-calendar-alt');
  const listAwrap = document.querySelectorAll('.item>span');
  const listDescription = document.querySelectorAll('.description');

  for (let i = 0; i < items.length; i += 1) {
    // push ID for new request
    arrId.push(items[i].id.videoId);

    listImg[oldSizeSlider + i].setAttribute('src', `${items[i].snippet.thumbnails.medium.url}`);
    listImg[oldSizeSlider + i].setAttribute('alt', 'альтернативный текст');
    listAwrap[oldSizeSlider + i].innerHTML = `${items[i].snippet.title}`;
    listChannelName[oldSizeSlider + i].parentElement.parentElement.lastElementChild.textContent = items[i].snippet.channelTitle;
    listUploadDate[oldSizeSlider + i].parentElement.parentElement.lastElementChild.textContent = items[i].snippet.publishedAt.slice(0, 10);
    listDescription[oldSizeSlider + i].textContent = `${items[i].snippet.description}`;
    // I must craete <a> here, because when i do it like other, that I can't him .setAttribute (it doesn't add in DOM inner <span> with text)
    const a = document.createElement('a');
    a.href = `https://www.youtube.com/watch?v=${items[i].id.videoId}`;
    a.setAttribute('target', '_blanck');
    listAwrap[oldSizeSlider + i].appendChild(a);
  }

  oldSizeSlider += items.length;

  // add new request for add view counter
  fetch(`https://www.googleapis.com/youtube/v3/videos?key=${key}&id=${arrId}&part=snippet,statistics`)
    .then(response => response.json()).then(res => addViewCount(res.items));
}


// Create slider
function buildSlider() {
  for (let i = 0; i < 15; i += 1) {
    addSliderItem();
  }
}


const pagination = document.createElement('div');
pagination.className = 'pagination';

function createPagination() {
  for (let i = 0; i < 4; i += 1) {
    const pag = document.createElement('span');
    pagination.appendChild(pag);
  }
}


export {
  slider,
  buildSlider,
  addInfo,
  createPagination,
  pagination,
};
