// this exeption because esLint against .js, but without him it is not working
/* eslint-disable import/extensions */
import search from './search.js';
import fillArrs from './clipsNumbers.js';
import {
  scrollLeft,
  scrollRight,
} from './scroll.js';

import {
  slider,
  buildSlider,
  addInfo,
  createPagination,
  pagination,
} from './slider.js';

let initSlider = false;
let xDown;
let paginationCount = 1;
let allItems;
let itemsOnPage = 4;
let pageToken;

// create wrapper
const wrapper = document.createElement('div');
document.body.appendChild(wrapper);
wrapper.classList.add('wrapper');

// add search in DOM
wrapper.appendChild(search);
document.addEventListener('DOMContentLoaded', () => search.firstElementChild.focus());
// add slider in DOM
wrapper.appendChild(slider);

// Search and request
const key = 'AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y';
const input = document.querySelector('input');

// get slider width
function getSliderWidth() {
  return slider.getBoundingClientRect().width;
}

function getAllItems() {
  allItems = document.querySelectorAll('.item');
}

// fill numbers in array. There may be more numbers
const arr4 = [];
const arr3 = [];
const arr2 = [];
const arr1 = [];

itemsOnPage = fillArrs(arr4, 10, itemsOnPage);
itemsOnPage = fillArrs(arr3, 13, itemsOnPage);
itemsOnPage = fillArrs(arr2, 20, itemsOnPage);
itemsOnPage = fillArrs(arr1, 40, itemsOnPage);
itemsOnPage = 4;
// let old = 4;

// function changePaginationCount() {
//   old = itemsOnPage;
//   console.log(old);
//   console.log(itemsOnPage);
// }

function getItemsOnPage() {
  itemsOnPage = getSliderWidth() / 370;
  // changePaginationCount();
}

window.addEventListener('resize', () => {
  getItemsOnPage();
});

// xInit like xDown, but esLint swears
function chooseSide(xInit, xUp) {
  const pags = document.querySelectorAll('.pagination span');
  const xWidth = getSliderWidth();
  if (xInit > xUp) {
    if (slider.scrollLeft >= xWidth * paginationCount - xWidth) {
      paginationCount += 1;
      scrollLeft(slider, xWidth);
      // eslint-disable-next-line no-use-before-define
      checkAddRequest();
    }
  }

  if (xInit < xUp) {
    if (paginationCount > 1) {
      if (slider.scrollLeft <= xWidth * paginationCount - xWidth) {
        paginationCount -= 1;
        scrollRight(slider, xWidth);
      }
    }
  }

  pags[2].textContent = `${paginationCount}`;
}


// get clientX in begin
slider.addEventListener('mousedown', (e) => {
  xDown = e.clientX;
});

// get clientX in finish and start scroll
let xUp;
slider.addEventListener('mouseup', (e) => {
  xUp = e.clientX;
  chooseSide(xDown, xUp);
});

function paginationBegin() {
  if (paginationCount > 1) {
    const pags = document.querySelectorAll('.pagination span');
    const xWidth = getSliderWidth();
    slider.scrollBy({
      left: `-${xWidth * paginationCount - xWidth}`,
      behavior: 'smooth',
    });
    paginationCount = 1;
    pags[2].textContent = `${paginationCount}`;
  }
}

// add pagination block
function fillPagination() {
  wrapper.appendChild(pagination);
  const pags = document.querySelectorAll('.pagination span');
  // to the begining
  pags[0].textContent = '<<';
  pags[0].addEventListener('click', () => {
    paginationBegin();
  });

  pags[1].textContent = '<';
  pags[1].addEventListener('click', () => {
    chooseSide(1, 10);
  });

  pags[2].textContent = `${paginationCount}`;

  pags[3].textContent = '>';
  pags[3].addEventListener('click', () => {
    chooseSide(10, 1);
  });
}

// start addInfo(in slider) and pagination (create and fill)
function request(inputValue) {
  fetch(`https://www.googleapis.com/youtube/v3/search?key=${key}&type=video&part=snippet&maxResults=15&q=${inputValue}`)
    .then(response => response.json())
    .then((res) => {
      addInfo(res.items, key, inputValue);
      pageToken = res.nextPageToken;
    })
    .then(() => {
      paginationBegin();
      getItemsOnPage();
    });
}

// handling input and start buildSlider()
input.addEventListener('change', () => {
  if (!initSlider) {
    initSlider = true;
    buildSlider();
    createPagination();
    fillPagination();
    getAllItems();
  }
  request(input.value);
});

function addRequest() {
  buildSlider();
  fetch(`https://www.googleapis.com/youtube/v3/search?key=${key}&type=video&part=snippet&maxResults=15&pageToken=${pageToken}&q=${input.value}`)
    .then(response => response.json())
    .then((res) => {
      addInfo(res.items, key);
      pageToken = res.nextPageToken;
    });
  getAllItems();
}

function checkAddRequest() {
  if (allItems.length - itemsOnPage * paginationCount <= itemsOnPage) {
    addRequest();
  }
}
