// fill numbers in array

let number;
function fillArrs(mas, n, itemsOnPage) {
  number = 1;
  for (let i = 0; i < n; i += 1) {
    mas.push([]);
    for (let j = 0; j < itemsOnPage; j += 1) {
      mas[i].push(number);
      number += 1;
    }
  }
  return itemsOnPage - 1;
}

export default fillArrs;
